import React, { Component } from 'react';
import '../App.css';
import '../Hidden.css';
import '../DictSelect.css';
import TextInput from './TextInput';
import ShowTask from './ShowTask';
import Select from 'react-select';
import 'react-select/dist/react-select.css';
// import Dictionary from './Dictionary';

// const file = require('../dictionary/project_gutenberg_top100.txt');

class Boyarin extends Component {
  state = {
    text: 'the quick brown fox jumps over the lazy dog',
    dictionary: 'the quick brown fox jumps over the lazy dog',
    toCheck: null,
    // currentText: null,
    error: null,
    // myTxt: null,
    newDictionary: null,
    file: require('../dictionary/project_gutenberg_top100.txt'),
    inputValue: '',
  }

  

  componentDidMount() {
    this.loadText(this.state.file);
  }

  handleDictSelect = (selectedOption) => {
    this.setState({ file: selectedOption.value }, () => this.loadText(this.state.file));
    // console.log(`Selected: ${selectedOption.label}`);
    // console.log(this.state.file);
  }

  loadText = path => {
    const xhr = new XMLHttpRequest();
    xhr.open('GET', path, true);
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== 4) return;
      const allText = xhr.responseText;
      // console.log(allText);
      this.setState({preTask: allText}, () => this.grabDictionary());
    }
    xhr.send(null);
  }

  grabDictionary = () => {
    const preTask = this.state.preTask
    const dictLength = preTask.split('\n').length;
    const firstRandomElement = Math.floor(Math.random() * (dictLength - 2)) + 1;
    const taskLength = 17;
    const newDict = preTask.split(/\r?\n/g);
    var preReadyDict = newDict[firstRandomElement] + ' ';
    for (let i = 1; i < taskLength; i++) {
      const randomElement = Math.floor(Math.random() * (dictLength - 2)) + 1;
      if (i !== (taskLength-1)) {
      preReadyDict = preReadyDict + newDict[randomElement] + ' ';
      } else {
        preReadyDict = preReadyDict + newDict[randomElement];
      }
    }
    this.setState({dictionary: preReadyDict});
  }

  textCheckHandler = event => {
    const enteredValue = event.target.value
    this.setState({
      toCheck: enteredValue,
      inputValue: enteredValue,
    });
    const dictionary = this.state.dictionary;
    const dictLength = dictionary.length;
    const enteredLength = enteredValue.length;
    const toCheck = dictionary.substr(0, enteredLength);

    // if (dictLength >= enteredLength && !dictionary.includes(enteredValue)){
    if (dictLength >= enteredLength && toCheck !== enteredValue){
    // if (toCheck !== enteredValue){
      this.setState({ error: 'Error' });
      // console.log(dictionary, enteredValue)
    } else if (dictLength< enteredLength && !enteredValue.includes(dictionary)){
      this.setState({error: 'Error'})
    } else {
      this.setState({error: null});
      // console.log(enteredValue, dictionary)
      if (dictLength < enteredLength) {
        // console.log('--', dictLength, enteredValue.length, enteredValue, dictionary)
        this.grabDictionary('newtext');
        this.setState({inputValue: ''});
      }
    }
  }

  render () {

    const { selectedOption } = this.state;
    const value = selectedOption && selectedOption.value;

    return (
        <div>
          <div>
            <Select
            // name='form-field-name'
            className='DictSelect'
            value={value}
            onChange={this.handleDictSelect}
            options={[
              { value: require('../dictionary/contemp_fiction_top100.txt'), label: 'Contemporary fiction top 100' },
              { value: require('../dictionary/contemp_fiction_top200.txt'), label: 'Contemporary fiction top 200' },
              { value: require('../dictionary/contemp_fiction_top300.txt'), label: 'Contemporary fiction top 300' },
              { value: require('../dictionary/contemp_fiction_top500.txt'), label: 'Contemporary fiction top 500' },
              { value: require('../dictionary/contemp_fiction_top1000.txt'), label: 'Contemporary fiction top 1000' },
              { value: require('../dictionary/contemp_fiction_top2000.txt'), label: 'Contemporary fiction top 2000' },
              { value: require('../dictionary/project_gutenberg_top100.txt'), label: 'Project Gutenberg top 100' },
              { value: require('../dictionary/project_gutenberg_top1000.txt'), label: 'Project Gutenberg top 1000' },
              { value: require('../dictionary/project_gutenberg_top2000.txt'), label: 'Project Gutenberg top 2000' },
              { value: require('../dictionary/project_gutenberg_top5000.txt'), label: 'Project Gutenberg top 5000' },
              { value: require('../dictionary/project_gutenberg_top10000.txt'), label: 'Project Gutenberg top 10000' },
              { value: require('../dictionary/rui_top100.txt'), label: 'Русский интернет-корпус топ 100' },
              { value: require('../dictionary/rui_top200.txt'), label: 'Русский интернет-корпус топ 200' },
              { value: require('../dictionary/rui_top500.txt'), label: 'Русский интернет-корпус топ 500' },
              { value: require('../dictionary/rui_top1000.txt'), label: 'Русский интернет-корпус топ 1000' },
              { value: require('../dictionary/rui_top2000.txt'), label: 'Русский интернет-корпус топ 2000' },
              { value: require('../dictionary/rui_top5000.txt'), label: 'Русский интернет-корпус топ 5000' },
              { value: require('../dictionary/rlnc_top100.txt'), label: 'Национальный корпус русского языка топ 100' },
              { value: require('../dictionary/rlnc_top200.txt'), label: 'Национальный корпус русского языка топ 200' },
              { value: require('../dictionary/rlnc_top500.txt'), label: 'Национальный корпус русского языка топ 500' },
              { value: require('../dictionary/rlnc_top1000.txt'), label: 'Национальный корпус русского языка топ 1000' },
              { value: require('../dictionary/rlnc_top2000.txt'), label: 'Национальный корпус русского языка топ 2000' },
              { value: require('../dictionary/rlnc_top5000.txt'), label: 'Национальный корпус русского языка топ 5000' },
              { value: require('../dictionary/rlnc_top10000.txt'), label: 'Национальный корпус русского языка топ 10000' },
            ]}
            />
          </div>
          <TextInput
            changed={(event) => {
              this.textCheckHandler(event)
              }}
            value={this.state.inputValue}
          />
        <div className='App'>
          <ShowTask task = {this.state.dictionary} />
        </div>
        <div className="App" >
          <div>
            {this.state.error}
          </div>
        </div>
      </div>
    )
  }
}

export default Boyarin;



