import React from 'react';

import '../App.css';

const textInput = ( props ) => {
    return (
        <div>
            <input 
                type="text" 
                onChange={props.changed} 
                value={props.value} 
                className="App" 
                autoFocus = {true}
            />
        </div>
    )
};

export default textInput;


